@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&display=swap);

@font-face {
    font-family: 'sk-modernistregular';
    src: url(../../static/media/sk-modernist-regular-webfont.19ddde1b.woff2) format('woff2'),
         url(../../static/media/sk-modernist-regular-webfont.2af458fa.woff) format('woff');
    font-weight: normal;
    font-style: normal;

}
:root{
  --primary-color: #313131;
  --secondary-color: #f1f1f1;
}

*{
  font-family: 'sk-modernistregular';
  margin:0;
  padding:0;
  box-sizing:border-box;
}

body,html{
  max-width: 100% !important;
  overflow-x:hidden;
}

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color:#313131;
  background-color:var(--primary-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
}

.playfair{
  font-family: 'Playfair Display', serif;
}

/* REV-BLOCK */

.rev-block {
  position: relative;
  overflow: hidden;
}
.rev-block > * {
  animation: fadeIn 0s 0.6s;
  animation-fill-mode: backwards;
}
.rev-block::after {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #313131;
  background: var(--primary-color);
  transform: translateX(-100%);
  animation: secondaryImageOverlayIn 0.6s 0s, secondaryImageOverlayOut 0.6s 0.6s;
  animation-fill-mode: both;
}



@keyframes fadeIn {
  0% {
      opacity: 0;
 }
  100% {
      opacity: 1;
 }
}
@keyframes secondaryImageOverlayIn {
  0% {
      width: 0;
 }
  100% {
      width: 100%;
 }
}
@keyframes secondaryImageOverlayOut {
  0% {
      transform: translateX(0);
 }
  100% {
      transform: translateX(101%);
 }
}


/* nav */

nav{
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    width: 100%;
    z-index:2000;
}

.navLogo img{
    width: 150px;
    height: auto;
    cursor: pointer;
}

.navInput{
    cursor:pointer;
    margin-right: 50px;
}

.menu-icon-line {
    height: 2px;
    width: 30px;
    display: block;
    background-color: var(--primary-color);
    margin-bottom: 7px;
    cursor: pointer;
    transform-origin: 0% 50%;
}

.menu-icon-line-left {
    width: 16.5px;
    transition: all 0.3s;
}

.menu-icon-line-right {
    width: 16.5px;
    float: right;
    transition: all 0.3s;
}

.fullWidth-nav{
    width: 30px;
}

.navInputActive .nav-transform-right{
    transform: rotate(-45deg) translate(0, +10px);
    width: 30px;
    transition: all 0.3s;
}

.navInputActive .nav-transform-middle{
  display: none;
}

.navInputActive .nav-transform-left{
  transform: rotate(45deg) translate(0, -10px);
    width: 30px;
    transition: all 0.3s;
}


#menu-nav{
    position: fixed;
    background-color: #F7F7F7;
    height: 100%;
    width: 100%;
    right:-100%; 
    transition: all 0.3s; 
    display: flex;
   justify-content: center;
}

.menu-nav-active{
    z-index: 50;
    right:0 !important;
    transition: all 0.3s;
    transition-delay: .2s;
}

.menu-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    width: 70%;
}

.menu-content li a{
    font-size: 45px;
    text-decoration:none;
    color: var(--primary-color);;
}

.menu-content ul{
    list-style-type: none;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.tracking-in-expand {
	animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
  @keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }
  

#media{
    display: flex;
    justify-content: space-around;
    width: 155px;
    position: absolute;
    bottom: 70px;
    left: 70px;
}

.slide-in-bottom {
	animation: slide-in-bottom 0.5s cubic-bezier(0.165, 0.840, 0.440, 1.000) both;
}
  @keyframes slide-in-bottom {
    0% {
      transform: translateY(1000px);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  .tilt-in-right-1 {
	animation: tilt-in-right-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
  @keyframes tilt-in-right-1 {
    0% {
      transform: rotateX(-30deg) translateX(300px) skewX(30deg);
      opacity: 0;
    }
    100% {
      transform: rotateX(0deg) translateX(0) skewX(0deg);
      opacity: 1;
    }
  }

  @media screen and (max-width: 768px) {
    .menu-content {
      align-items: center;
  }

}
