/* responsive */

@media(min-width: 760px) {
	.focusImg {
		background-position-y: -400px;
		background-attachment: fixed;
		background-position-x: 800px;
		background-repeat: no-repeat;
	}
}


@media(max-width: 760px) {
	.focus-focus-container {
		flex-direction: column;
	}
	.desc-content-container {
		flex-direction: column;
		align-items: flex-start;
		width: 90%;
	}
	.desc-txt {
		width: 100%;
	}
	.desc-clear {
		margin-top: 25px;
		margin-bottom: 15px;
	}
	.item {
		margin-bottom: 10px;
	}
	.img-focus-container {
		width: 100%;
		height: 500px;
		background-size: cover;
	}
	.list-content {
		height: 580px;
	}
	.img-dev,
	.img-focus {
		height: 300px;
		width: 300px;
		background-color: rgb(92, 92, 92);
	}
	.dev,
	.focus {
		height: 400px;
	}
	.focusImg {
		background-attachment: fixed;
	}
	.header-container h1 {
		font-size: 4rem;
	}
	.single-img img{
		margin-top: 15px;
		width: 90%;
		height: auto;
		border-radius: 11px;
	}
	.title-focus-container h2{
		font-size: 4em;
	}
}
