.me-container{
    background-color: #F7F7F7;
    /* height: 400px; */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.me-content-container{
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.me-text-container{
    height: 300px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}

.me-text-container h3{
    color:var(--primary-color);
    font-size: 30px;
}

.me-text-container p{
    color:var(--primary-color);
    font-size: 23px;
    width: 655px;
}

.me-text-container .playfair{
    text-decoration: none;
    color:var(--primary-color);
}

.download a{
    color:var(--primary-color);
}

.me-container img{
    width: 600px;
    height: auto;
}

@media screen and (max-width: 768px) {
    .me-container{
        height: auto;
        display: flex;
        justify-content: center;
        padding-top: 25px;
        padding-bottom: 25px;
    }
    .me-text-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .me-content-container{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .me-text-container{
        width: 80%;
        text-align: center;
        height: 554px;
    }

    .me-text-container p{
        width: 100%;
        line-height: 1.5em;
        margin-bottom: 25px;
    }

    .me-text-container h3{
        margin-bottom: 25px;
    }

    .me-image-container{
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .me-image-container img{
        width: 80%;
    }
    .me-image-container .rev-block{
        display: flex;
        justify-content: center;
    }
}