.banner-container{
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.banner-container h1{
    color:var(--primary-color);
    font-weight: 300;
    font-size: 4rem;
}

.banner-container span{
    color:var(--primary-color);
    font-weight: 800 !important;
}

.banner-container h2{
    color:var(--primary-color);
    font-weight: 300;
}

.banner-content-container{
    /* height: 130px; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 768px) {
    .banner-container{
        text-align: center;
    }
    .banner-container h1{
        font-size: 3rem;
    }
    /* .banner-content-container{
        height: 220px;
    } */
}

@media screen and (max-width : 1228px){
    .banner-container{
        /* height:203px; */
        text-align: center;
    }
}