.mentions-container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.mentions-title-container{
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mentions-container .mentions{
    width: 65%;
}

.mentions h3{
    margin-bottom:20px;
    margin-top: 20px;
}

.mentions p:last-child{
    margin-bottom: 50px;
}