.footer-container{
    height: 80px;
    background-color: #F7F7F7;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-content-container{
    width: 80%;
    display: flex;
    justify-content: space-between;
}

.footer-content-container a{
    color:var(--primary-color);
    text-decoration: none;
}

.social-media img{
    height: 25px;
    margin-left:15px;
}