#folio{
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.folio-container{
    min-height: 100vh;
}

.folio-container-single{
    margin:35px;
}

.folio-title-container{
    height: 220px;
    display: flex;
    align-items: center;
}

.folio-content-container{
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.folioSingle img{
    height: 400px;
    width: 400px;
    object-fit: cover;
    border-radius: 11px;
}

@media screen and (max-width: 768px) {
    .folioSingle img{
        height: 330px;
        width: 330px;
    }
}