/* nav */

nav{
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    width: 100%;
    z-index:2000;
}

.navLogo img{
    width: 150px;
    height: auto;
    cursor: pointer;
}

.navInput{
    cursor:pointer;
    margin-right: 50px;
}

.menu-icon-line {
    height: 2px;
    width: 30px;
    display: block;
    background-color: var(--primary-color);
    margin-bottom: 7px;
    cursor: pointer;
    transform-origin: 0% 50%;
}

.menu-icon-line-left {
    width: 16.5px;
    transition: all 0.3s;
}

.menu-icon-line-right {
    width: 16.5px;
    float: right;
    transition: all 0.3s;
}

.fullWidth-nav{
    width: 30px;
}

.navInputActive .nav-transform-right{
    transform: rotate(-45deg) translate(0, +10px);
    width: 30px;
    transition: all 0.3s;
}

.navInputActive .nav-transform-middle{
  display: none;
}

.navInputActive .nav-transform-left{
  transform: rotate(45deg) translate(0, -10px);
    width: 30px;
    transition: all 0.3s;
}


#menu-nav{
    position: fixed;
    background-color: #F7F7F7;
    height: 100%;
    width: 100%;
    right:-100%; 
    transition: all 0.3s; 
    display: flex;
   justify-content: center;
}

.menu-nav-active{
    z-index: 50;
    right:0 !important;
    transition: all 0.3s;
    transition-delay: .2s;
}

.menu-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    width: 70%;
}

.menu-content li a{
    font-size: 45px;
    text-decoration:none;
    color: var(--primary-color);;
}

.menu-content ul{
    list-style-type: none;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.tracking-in-expand {
	-webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

@-webkit-keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }
  

#media{
    display: flex;
    justify-content: space-around;
    width: 155px;
    position: absolute;
    bottom: 70px;
    left: 70px;
}

.slide-in-bottom {
	-webkit-animation: slide-in-bottom 0.5s cubic-bezier(0.165, 0.840, 0.440, 1.000) both;
	        animation: slide-in-bottom 0.5s cubic-bezier(0.165, 0.840, 0.440, 1.000) both;
}

@-webkit-keyframes slide-in-bottom {
    0% {
      -webkit-transform: translateY(1000px);
              transform: translateY(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
  }
  @keyframes slide-in-bottom {
    0% {
      -webkit-transform: translateY(1000px);
              transform: translateY(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
  }
  
  .tilt-in-right-1 {
	-webkit-animation: tilt-in-right-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: tilt-in-right-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes tilt-in-right-1 {
    0% {
      -webkit-transform: rotateX(-30deg) translateX(300px) skewX(30deg);
              transform: rotateX(-30deg) translateX(300px) skewX(30deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateX(0deg) translateX(0) skewX(0deg);
              transform: rotateX(0deg) translateX(0) skewX(0deg);
      opacity: 1;
    }
  }
  @keyframes tilt-in-right-1 {
    0% {
      -webkit-transform: rotateX(-30deg) translateX(300px) skewX(30deg);
              transform: rotateX(-30deg) translateX(300px) skewX(30deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateX(0deg) translateX(0) skewX(0deg);
              transform: rotateX(0deg) translateX(0) skewX(0deg);
      opacity: 1;
    }
  }

  @media screen and (max-width: 768px) {
    .menu-content {
      align-items: center;
  }

}