/* focus page */

.focus-focus-container {
	padding-top: 50px;
	display: flex;
	width: 100%;
	justify-content: center;
	justify-content: space-between
}

.title-focus-container {
	display: flex;
	align-items: flex-end;
}

.title-focus-container h2 {
	color:var(--primary-color);
	font-size: 5em;
}

.img-focus-container {
	height: 700px;
	width: 1000px;
	object-fit: contain;
}

.large-desc {
	width: 100%;
	display: flex;
	justify-content: center;
}

.desc-content-container {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	/* height: 215px; */
	width: 67%;
	margin-top: 45px;
	margin-bottom: 45px;
}

.desc-clear {
	height: 150px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.desc-txt {
	width: 50%;
}

.desc-txt p {
	line-height: 1.5em;
}

.list-content {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.focusFirst {
	width: 70%;
}

.wrapper {
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.wrapper .container {
	width: 96%;
	margin: 0px auto;
}

.pdfProject-container{
	width: 100%;
	display: flex;
	justify-content: center;
}

.pdfProject{
	color:var(--secondary-color);
	background-color:var(--primary-color);
	border:none;
	font-weight: 500;
	font-size: 20px;
	padding: 10px 20px 10px 20px;
	border-radius:8px;
	cursor:pointer;
	margin-bottom: 70px;
	margin-top: 20px;
}

.pdfProject:hover{
	color:var(--primary-color);
	background-color:var(--secondary-color);
	transition: all 0.5s;
}

.single-content-container{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.single-img{
	width: 100%;
	display: flex;
	justify-content: center;
	margin-top: 40px;
}

.single-img img{
	width: 60%;
	height: auto;
	border-radius: 11px;
}

.focusImg{
	object-fit:cover;
}

/* responsive */

@media(min-width: 760px) {
	.focusImg {
		background-position-y: -400px;
		background-attachment: fixed;
		background-position-x: 800px;
		background-repeat: no-repeat;
	}
}


@media(max-width: 760px) {
	.focus-focus-container {
		flex-direction: column;
	}
	.desc-content-container {
		flex-direction: column;
		align-items: flex-start;
		width: 90%;
	}
	.desc-txt {
		width: 100%;
	}
	.desc-clear {
		margin-top: 25px;
		margin-bottom: 15px;
	}
	.item {
		margin-bottom: 10px;
	}
	.img-focus-container {
		width: 100%;
		height: 500px;
		background-size: cover;
	}
	.list-content {
		height: 580px;
	}
	.img-dev,
	.img-focus {
		height: 300px;
		width: 300px;
		background-color: rgb(92, 92, 92);
	}
	.dev,
	.focus {
		height: 400px;
	}
	.focusImg {
		background-attachment: fixed;
	}
	.header-container h1 {
		font-size: 4rem;
	}
	.single-img img{
		margin-top: 15px;
		width: 90%;
		height: auto;
		border-radius: 11px;
	}
	.title-focus-container h2{
		font-size: 4em;
	}
}