.slider-container{
    height: 700px;
}

.slider-container h3{
    color:var(--primary-color);
    margin-top: 120px;
    margin-left: 120px;
    font-size: 30px;
}

.slider {
	 transform: translateY(-50%);
	 display: flex;
     margin-top: 210px;
     margin-left: 320px;
     z-index: 10 !important;
     cursor: all-scroll !important;
}

.slider a{
    cursor: pointer;
}

.slider .slide:not(:first-child) {
	 margin-left: 6vw;
}
.slider .slide .preview {
	 height: 350px;
	 width: 350px;
	 background: #c4c4c4;
	 overflow: hidden;
     border-radius: 11px;
}
.slider .slide .preview img {
	 width: 100%;
	 height: 100%;
	 object-fit: cover;
	 object-position: center;
     border-radius: 11px;
}

@media screen and (max-width: 768px) {
    .slider-container h3{
        margin-top: 70px;
        margin-left: 30px;
        font-size: 30px;
    }
    .slider{
        margin-left: 50px;
    }
}